html {
    background: #2a2a2e;
    color: #f9f9fa;
    user-select: none;
    overflow-x: hidden;
}

.search {
    width: 50%;
    border: solid 1px #f9f9fa33;
    background: #38383D;
    box-shadow: 0 1px 4px 0 rgba(12, 12, 13, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.15);
    font: inherit;
    color: inherit;
    border-radius: 3px;
    padding: 10px;
    margin: 10px auto;
    display: block;
    outline: none;
}
.search:focus {
    box-shadow: 0 0 0 1px #45A1FF, 0 0 0 4px rgba(69, 161, 255, 0.3), 0 0 0 1px #45A1FF;
}

.entries {
    display: flex;
    flex-flow: wrap;
    width: 100%;
    opacity: 0;
    transition: .2s ease;
}
.entries .entry {
    padding: 20px;
    background: #38383d;
    border-radius: 10px;
    margin: 10px;
    box-shadow: 0 1px 4px 0 rgba(12, 12, 13, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.15), inset 0 0 0 1px #8c180f;
    cursor: pointer;
    transition: .2s ease;
}
.entries .entry#green {
    box-shadow: 0 1px 4px 0 rgba(12, 12, 13, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.15), inset 0 0 0 1px #34a853;
}

.buttons {
    display: flex;
}
.buttons .button {
    background: #4A4A4F;
    padding: 10px;
    border-radius: 10px;
    margin: 10px;
    text-align: center;
    box-shadow: 0 1px 4px 0 rgba(12, 12, 13, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.15);
    cursor: pointer;
    height: max-content;
    transition: .2s ease;
}
.buttons .button:hover {
    background: #56565C;
}

.about {
    padding: 15px 0;
    color: #f9f9fa6e;
    margin: -97px -97px 97px;
    position: relative;
    left: 97px;
    top: 97px;
}
.about p {
    margin: 0;
}

.right {
    width: 0;
    right: 0;
    transition: .2s ease;
    height: 100%;
    position: absolute;
    top: 73px;
    margin: 0 40px 40px;
    opacity: 0;
}
.right .title {
    margin-top: 0;
    font-size: 22px;
}
.right .add {
    position: absolute;
    right: -15px;
    top: -8px;
    padding: 10px;
    cursor: pointer;
    background: #4a4a4f;
    border-radius: 10px;
}
.right .wrapper {
    width: calc(100% - 15px);
    background: #38383d;
    padding: 15px;
    border-radius: 10px;
}
.right .wrapper .entry {
    padding: 10px;
    background: #4a4a4f;
    border-radius: 10px;
    margin: 10px 0;
}
.right .wrapper .entry .button {
    float: right;
    background: #3e8649;
    padding: 10px;
    border-radius: 10px;
    position: relative;
    top: -10px;
    left: 10px;
    cursor: pointer;
}